@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  .text-neonOrange {
    color: #ff7f00; /* Neon turuncu */
    text-shadow: 0 0 5px #ff7f00, 0 0 10px #ff7f00, 0 0 15px #ff7f00,
      0 0 20px #ff7f00;
  }

  @keyframes pulse3d {
    0% {
      text-shadow: 0 0 5px #ff7f00, 0 0 10px #ff7f00, 0 0 15px #ff7f00,
        0 0 20px #ff7f00;
      transform: scale(1);
    }
    50% {
      text-shadow: 0 0 10px #ff7f00, 0 0 20px #ff7f00, 0 0 30px #ff7f00,
        0 0 40px #ff7f00;
      transform: scale(1.1);
    }
    100% {
      text-shadow: 0 0 5px #ff7f00, 0 0 10px #ff7f00, 0 0 15px #ff7f00,
        0 0 20px #ff7f00;
      transform: scale(1);
    }
  }

  .animate-pulse3d {
    animation: pulse3d 2s infinite;
  }
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: white;

  color: #ffffff;
}

#root {
  min-height: 100vh;
}

/* Tüm uygulama container'ı */
#appContainer {
  background-color: #191e27;
  min-height: 100vh;
  color: white;
  font-family: Arial, sans-serif;
  display: flex;
  flex-direction: column;
}

#header {
  display: flex;
  justify-content: flex-start; /* Sol tarafa hizalama */
  align-items: center; /* Yükseklik olarak ortalama */
  padding: 16px;
  background-color: #ffffff35;
  height: 180px;
}

#container {
  padding-top: 180px;
}

#contentArea {
  padding-top: 100px; /* #mainContent'in üst kısmındaki padding'i taklit eder */
  grid-template-columns: repeat(
    auto-fill,
    minmax(200px, 1fr)
  ); /* Grid düzeni ayarlarını kontrol et */
}

#logo {
  width: 300px;
  height: 300px;
  margin-right: 200px; /* Logo ile arama kutusu arasında boşluk */
  margin-bottom: 55px;
}

/* Navbar yazısı */
#headerText {
  color: lightgray;
  font-size: 1.2rem;
  font-weight: bold;
}

/* Main content alanı */
#mainContent {
  padding-top: 100px; /* Header ile çakışmasın */
  padding-left: 20px;
  padding-right: 20px;
  flex: 10;
}

#specialText p {
  color: #cccccc;
  font-style: italic;
  margin-bottom: 15px;
}

/* İçerik alanı */
#contentArea {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
}

@media (min-width: 768px) {
  #contentArea {
    grid-template-columns: 2fr 1fr;
  }
}

/* Şarkı listesi alanı */
#songListArea {
  background-color: #000000;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
}

/* Player alanı */
#playerArea {
  background-color: #000000;
  padding: 20px;
  border-radius: 10px;
  color: white;
}

/* Hata kutusu */
#errorBox {
  background-color: red;
  color: white;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 15px;
  animation: shake 0.5s;
}

/* Hata kutusuna titreme efekti */
@keyframes shake {
  0% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(-5px);
  }
  50% {
    transform: translateX(5px);
  }
  75% {
    transform: translateX(-5px);
  }
  100% {
    transform: translateX(0);
  }
}

/* Footer */
#footer {
  margin-top: 50px;
  padding: 20px;
  text-align: center;
  color: gray;
  border-top: 1px solid #333;
  background-color: #000;
}

/* SearchBar Container */
.search-bar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}

.search-input {
  width: 100%;
  max-width: 250px; /* Genişlik sınırı */
  padding: 10px;
  background-color: #333;
  color: white;
  border: 2px solid #555;
  border-radius: 10px;
  font-size: 16px;
}

.search-input:focus {
  outline: none;
  border-color: #1a73e8;
}

/* Button Stili */
.search-button {
  padding: 10px 20px;
  background-color: #1a73e8;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 16px;
}

.search-button:hover {
  background-color: #1669c1; /* Hover efektinde biraz daha koyu */
}

#searchSection {
  display: flex;
  flex-grow: 1; /* Sağdaki alanı genişletir */
  justify-content: flex-start; /* Başlangıç noktasına hizalama */
  align-items: center; /* Yükseklik olarak ortalama */
  height: 1px;
  border-radius: 20px;
  background-color: #555;
}

#smg {
  width: 85vh;
  height: 25px;
  background-color: #1f1f1fa8;
  border-radius: 6px;
  text-align: center;
  color: #cccccc;
}

#btn {
  background-color: #1f1f1f;
  color: #cccccc;
  border-radius: 6px;
  width: 50px;
  height: 30px;
  margin-top: -2px;
  padding-top: 5px;
}

#searchSection {
  border: none;
  outline: none;
  box-shadow: none;
  background: none;
}

@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.container {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.vinyl {
  display: flex;
  align-items: center;
  justify-content: center;
}

.vinyl-circle {
  position: relative;
  width: 468px;
  height: 468px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: repeating-radial-gradient(
    circle,
    #000,
    #000 4px,
    #131313 4px,
    #131313 5.5px
  );
  border-radius: 50%;
  animation: rotating 4s linear infinite; /* Bu animasyon, vinilin dönüşünü sağlar */
}

.vinyl-circle::before {
  position: absolute;
  width: 149px;
  height: 149px;
  background-image: url("https://igorosa.github.io/gramophone/img/vinyl5.jpg");
  background-repeat: no-repeat;
  background-position: top left;
  background-size: 103%;
  border: 11px solid #000000;
  border-radius: 50%;
  content: "";
}

.vinyl-glare {
  position: absolute;
  display: none;
  width: 468px;
  height: 468px;
  background: radial-gradient(
      farthest-side ellipse at top,
      #ffffff35,
      transparent 50%
    ),
    radial-gradient(farthest-side ellipse at bottom, #ffffff35, transparent 50%);
  border-radius: 50%;
  transform: rotate(45deg);
}

.vinyl-center {
  position: absolute;
  width: 290px;
  height: 290px;
  background: #000000;
  box-shadow: inset -1px 1px 2px 0px #000000, -3px 3px 4px -1px #000000;
  border-radius: 50%;
  content: "";
}

/* Burada resmin dönüşü senkronize ediliyor */
.vinyl-thumbnail {
  position: absolute;
  width: 250px; /* Thumbnail boyutunu ayarlayabilirsiniz */
  height: 250px;
  border-radius: 50%;
  object-fit: cover;
  animation: rotating 40 infinite; /* Aynı dönüş animasyonunu ekliyoruz */
  top: 5%; /* Ortalamak için */
  left: 5%; /* Ortalamak için */
  transform: translate(-50%, -50); /* Gerçekten ortalanması için */
}
